//react 
import * as React from "react";
import { ListView, SearchBar, Toast } from 'antd-mobile';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import * as Actions from '../../actions';

import SellItem from '../../components/sellItem'
import { getSellList } from '../../api/sell'
import $ from 'jquery'
import './index.less'


const mapStateToProps = (state: any) => {
    return {
        store: state
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        ...bindActionCreators(Actions, dispatch)
    }
}

interface ISprops {
    [propName: string]: any,
}
interface IState {
    dataSource: any,
    isLoading: Boolean,
    hasMore: Boolean,
    query: any,
}


class CourseList extends React.Component<ISprops, IState> {
    constructor(props: any) {
        super(props);
        const dataSource = new ListView.DataSource({
            rowHasChanged: (row1, row2) => row1 !== row2,
        });
        this.state = {
            dataSource,
            isLoading: true,
            hasMore: true,
            query: ''
        }
    }
    courseList: Object[] = [];
    page_num: number = 1;
    page_size: number = 10;

    componentWillMount() {
        document.title = '';
        this.queryListData()
    }
    componentDidMount() {
    }
    queryListData() {
        this.setState({ isLoading: true });
        Toast.loading('');
        let {page_num, page_size} = this;
        let {query} = this.state;
        getSellList({
            page_num,
            page_size,
            course_name: query
        }).then( (res) => {
            Toast.hide()
            if(!res.status) {
                let {list, total_pages, page_num} = res.data;
                // 
                if (page_num === 1) {
                    this.courseList = []
                }
                this.courseList = [...this.courseList, ...list];
                
                // 1
                if(page_num < total_pages) {
                    this.page_num += 1
                }

                this.setState({
                    dataSource: this.state.dataSource.cloneWithRows(this.courseList),
                    isLoading: false,
                    hasMore: page_num < total_pages
                })
            } else if (res.status === 4001) {
                this.props.history.push('/sell/sellSign')
            }
        })
    }
    onEndReached = (event) => {
        if (this.state.isLoading || !this.state.hasMore) {
            return;
        }
        this.queryListData()
    }
    clearQuery= () => {
        //searchbar 
        $('.am-search-synthetic-ph-placeholder').css({'color':'#bbb'})
        this.setState({
            query:''
        })
    }
    changeSearch = (e) => {
        this.setState({
            query: e
        })
        $('.am-search-synthetic-ph-placeholder').css({'color':'#EDEDED'})
    }
    changeBlur = () => {
        this.clearQuery()
    }
    changeCancel = () => {
        this.clearQuery()
        this.setState({
            query:''
        },() => {
            this.page_num = 1;
            this.queryListData()
        })
    }
    handleSubmit = (v) => {
        this.page_num = 1;
        this.queryListData()
    }

    render() {
        const renderFooter = () => (
            this.state.hasMore
            ? 
            <div style={{ textAlign: 'center', lineHeight: '.26rem' }}>{this.state.isLoading ? 'Loading...' : 'Loaded'}</div>
            :
            <div></div>
        )
        const Row = (rowData, sectionID, rowID) => (
            <SellItem key={rowID} course={rowData}/>
        )
        const {query} = this.state;
        const { courseList } = this;
        return (
            <div className="sell-list" style={{paddingTop:'.5rem'}}>
                <div className="sell-top">
                    <SearchBar
                        value={query}
                        placeholder=""
                        onSubmit={ (val) => this.handleSubmit(val)}
                        onBlur={this.changeBlur}
                        className="search-bar"
                        onChange={(e) => this.changeSearch(e)}
                        showCancelButton={false}
                        onCancel={this.changeCancel}
                    />
                </div>
                
                <ListView
                    dataSource={this.state.dataSource}
                    renderFooter={courseList.length === 0 ? () => (<div></div>) : renderFooter}
                    renderRow={Row}
                    className="index-list"
                    pageSize={4}
                    useBodyScroll
                    onScroll={() => { console.log('scroll'); }}
                    scrollRenderAheadDistance={500}
                    onEndReached={this.onEndReached}
                    onEndReachedThreshold={10}
                />
            </div>
        );
    }

}
export default connect(mapStateToProps, mapDispatchToProps)(CourseList);