import * as React from "react";
import {withRouter} from "react-router";
import { getSeatActiveTime } from '../util'
import '../assets/less/sellItem.less'

interface ISprops {
    [propName: string]: any,
}
interface IState {
}
class SellItem extends React.Component<ISprops, IState> {

    handleClickList = (id: string) => {
        //
        this.props.history.push('/sell/detail/'+id)
    }
    render() {
        const { course } = this.props;
        const time = getSeatActiveTime(course.start_time || '',course.end_time || '')
        return (
            <div className="sell-item clearfix" key={course.id} onClick={() => this.handleClickList(course.id)}>
                <div className="sell-item-l left">
                    <h2>{course.class_title}</h2>
                    <p className="time">{time}</p>
                    <p>{!course.province ? course.city : `${course.province}-${course.city}`}</p>
                </div>
                <div className="sell-item-r right">
                    <img src={course.img_url} alt={course.course_name} />
                </div>
            </div>
        )
    }

}

//@ts-ignore
export default withRouter(SellItem);
